import React from "react";
import styled, { css } from "styled-components";
import Container from "react-bootstrap/Container";

import { TransformWrapper, TransformComponent } from "react-zoom-pan-pinch";

//import "./App.css";

import { VectorMap } from "@south-paw/react-vector-maps";
import world from "./world";

import SideBar from "./components/sidebar";

class App extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      country: "",
      confirmed: 0,
      deaths: 0
    };

    //
    // this.onInputChange = this.onInputChange.bind(this);
  }

  createCSS() {
    let styles = "";

    for (let i = 0; i < world.layers.length; i += 1) {
      styles +=
        `

           &[id=\"` +
        world.layers[i].id +
        `\"] {
             fill: ` +
        world.layers[i].color +
        `

         }
       `;
    }

    console.log(styles);

    return css`
      ${styles}
    `;
  }

  render() {
    //console.log(world.layers.length);
    //this.Mtest();
    //
    // const numberFormat = new Intl.NumberFormat("en", {
    //   style: "decimal"
    // }).format(value);

    const Map = styled.div`
      margin: 1rem auto;
      width: 850px;
      align: right;

      svg {
        stroke: #fff;

        // All layers are just path elements
        path {
          fill: green;
          cursor: pointer;
          outline: none;

          // When a layer is hovered
          &:hover {
            fill: rgba(168, 43, 43, 0.83);
          }

          // When a layer is focused.
          &:focus {
            fill: rgba(168, 43, 43, 0.6);
          }
        }
      }
    `;

    const layerProps = {
      onMouseEnter: ({ target }) => {
        //console.log(target);
        this.setState({
          country: target.attributes.name.value,
          confirmed: new Intl.NumberFormat("en", {
            style: "decimal"
          }).format(target.attributes.confirmed.value),
          deaths: new Intl.NumberFormat("en", {
            style: "decimal"
          }).format(target.attributes.deaths.value)
        });
        //console.log(this.state.country);
      },
      onMouseLeave: ({ target }) =>
        this.setState({ country: "", confirmed: 0, deaths: 0 }),
      onClick: ({ target }) => console.log(target.attributes.name.value)
    };

    return (
      <div className="mainbody">
        <Container fluid>
          <div className="splash">
            <h2> Coronavirus Disease 2020 </h2>
            <h4 className="cname"> Country: {this.state.country}</h4>
            <h4 className="datainfo">
              {" "}
              Confirmed Cases: {this.state.confirmed} <br />
              Deaths: {this.state.deaths}{" "}
            </h4>
            <small>
              Data Source:{" "}
              <a href="https://github.com/CSSEGISandData/COVID-19">
                CSSEGISandData{" "}
              </a>{" "}
              Last Update: {world.updateDate}
            </small>
          </div>
        </Container>
        <Container>
          <SideBar />
          <TransformWrapper>
            {({ zoomIn, zoomOut, resetTransform, ...rest }) => (
              <React.Fragment>
                <div className="tools" align="right">
                  <button onClick={zoomIn}>+</button>
                  <button onClick={zoomOut}>-</button>
                  <button onClick={resetTransform}>x</button>
                </div>
                <TransformComponent>
                  <Map>
                    <VectorMap
                      {...world}
                      // onMouseEnter={({ target }) => {
                      //   console.log("Div is clicked", target.attributes.name.value);
                      // }}
                      layerProps={layerProps}
                    />
                  </Map>
                </TransformComponent>
              </React.Fragment>
            )}
          </TransformWrapper>
        </Container>
      </div>
    );
  }
}

export default App;
